import React, { Component } from 'react';
import { NavLink, Link } from 'react-router-dom';
import { Tween, Timeline } from 'react-gsap';
import { TweenMax } from 'gsap';

class Header extends React.Component { 
    constructor(props){
        super(props);
        this.state = {
            isMenuOpen: false,
            isMobile: false,
        }

        this.nav = null;
        this.menuTween = null;
    }
    
    componentDidMount() {

        this.menuTween = TweenMax.fromTo(this.nav, .4, 
            {
                y:-50,
                x:0,
                opacity:0,
                autoAlpha:0
            },
            {
                y:0,
                x:0,
                opacity:1,
                autoAlpha:1
            },
        );

        this.menuTweenMobile = TweenMax.fromTo(this.nav, .1, 
            {
                x:-30,
                y:0,
                opacity:0,
                autoAlpha:0
            },
            {
                x:0,
                y:0,
                opacity:1,
                autoAlpha:1
            },
        );

        // 초기 트윈 셋팅 pause
        this.menuTween.pause();
        this.menuTweenMobile.pause();


        /* 
            mobile Size Check 480
        */

        // 1. initial check
        let winWid = window.innerWidth;
        if(this.state.isMobile){
            if(winWid > 480){
                this.setState({
                    isMobile: false
                });
            }
        } else {
            if(winWid <= 480){
                this.setState({
                    isMobile: true
                });
            }
        }

        // 2. resize check 
        let _this = this; //안에서 this 사용하기 위함
        window.addEventListener('resize', function(){
            let winWid = window.innerWidth;

            if(_this.state.isMobile){
                if(winWid > 480){
                    _this.setState({
                        isMobile: false
                    });
                }
            } else {
                if(winWid <= 480){
                    _this.setState({
                        isMobile: true
                    });
                }
            }
        });

        // 메뉴외부영역 클릭시 메뉴닫기
        document.addEventListener('click', (e) => {
            const isMenu = e.target.className == 'active menu' || e.target.className == 'line';
            if(this.state.isMenuOpen && !isMenu){
                this.setState({
                    isMenuOpen: false
                });

                if(this.state.isMobile){
                   this.menuTweenMobile.reverse();
                } else {
                    this.menuTween.reverse();
                }
            }
        });

    }

    menuToggle = () => {

        this.setState({
            isMenuOpen: !this.state.isMenuOpen
        });
        
        // mobile 모션 분기
        if(this.state.isMobile){
            !this.state.isMenuOpen ? this.menuTweenMobile.play() : this.menuTweenMobile.reverse();
        } else {
            !this.state.isMenuOpen ? this.menuTween.play() : this.menuTween.reverse();
        }
        
        // console.log(this.state.isMenuOpen);
    }


    render(){
        return (
            <header>

                <div className="hd_inner">
                    <Link to="/">
                        <h1 className="logo">ADCAPSULE</h1>
                    </Link>

                    <button className={`${!this.state.isMenuOpen ? '' : 'active'} menu`} onClick={this.menuToggle}>
                        <span className="line"></span>
                        <span className="hide">Menu</span>
                    </button>

                    {/* ref */}
                    <nav ref={nav => this.nav = nav}>
                        <ul>
                            {/* <li><NavLink onClick={this.menuToggle} exact to="/" activeClassName="active">Home</NavLink></li> */}
                            <li><NavLink onClick={this.menuToggle} to="/Work" activeClassName="active">Our Work</NavLink></li>
                            <li><NavLink onClick={this.menuToggle} to="/About" activeClassName="active">About ADC</NavLink></li>
                            <li><NavLink onClick={this.menuToggle} to="/Notice" activeClassName="active">Notice</NavLink></li>
                            <li><NavLink onClick={this.menuToggle} to="/Contact" activeClassName="active">Contact</NavLink></li>
                            <li><NavLink onClick={this.menuToggle} to="/Guide" activeClassName="active">Guide</NavLink></li>

                            {/* <li><NavLink exact to={`${process.env.PUBLIC_URL}/`} activeClassName="active">Home</NavLink></li>
                            <li><NavLink to={`${process.env.PUBLIC_URL}/Guide`} activeClassName="active">Guide</NavLink></li>
                            <li><NavLink to={`${process.env.PUBLIC_URL}/Work`} activeClassName="active">Work</NavLink></li>
                            <li><NavLink to={`${process.env.PUBLIC_URL}/Contact`} activeClassName="active">Contact</NavLink></li> */}
                        </ul>
                        <button className="nav_close" onClick={this.menuToggle}><span className="hide">Close Nav</span></button>
                    </nav>
                        
                </div>
            </header>
        )
    }

};

export default Header;