/****************************/
/* upload                     */
/****************************/

export default function Upload(){

    /****************************/
    /* Input - File Upload      */
    /****************************/
    const uploadBtn = document.getElementById('uploadBtn');
    uploadBtn.addEventListener('change', (e) =>{
        
        let filename = e.target.value;
        let lastIndex = filename.lastIndexOf("\\");
        if (lastIndex >= 0) {
            filename = filename.substring(lastIndex + 1);
        }
        let files = e.target.files;

        // console.log(files.length);

        // 업로드파일 제한 3개
        if(files.length < 4){
            const uploadLeng = document.querySelector('#uploadList').querySelectorAll('.list').length;

            // 추가 업로드시 length 체크
            if(uploadLeng + files.length < 4){
                for (let i = 0; i < files.length; i++) {
        
                    const fileList = document.createElement('div');
                    const fileTxtWrap = document.createElement('span');
                    const fileTxt = document.createTextNode(files[i].name);
                    const close = document.createElement('a');
        
                    fileList.appendChild(fileTxtWrap);
                    fileList.classList.add('list');
                    fileTxtWrap.appendChild(fileTxt);
                    fileList.appendChild(close);
                    close.setAttribute('href','#none');
                    close.classList.add('close');
        
                    document.getElementById('uploadList').appendChild(fileList);
                }
            } else {
                // console.log('over list');
                alert('첨부파일은 최대 3개까지 첨부가능합니다.');
                e.preventDefault();
            }
    
        } else {
            // console.log('over select');
            alert('첨부파일은 최대 3개까지 첨부가능합니다.');
            e.preventDefault();
        }
        
        const close = document.querySelectorAll('.close');
        Array.prototype.forEach.call(close, (el, i) => {
            el.addEventListener('click', (e) => {
                e.target.parentNode.remove();
            });
        });
    });


    // 이미 업로드 갯수가 3개 일때
    uploadBtn.addEventListener('click', (e) =>{
        const uploadLeng = document.querySelector('#uploadList').querySelectorAll('.list').length;
        // console.log(uploadLeng);
        if(uploadLeng == 3){
            // console.log('full');
            
            alert('첨부파일은 최대 3개까지 첨부가능합니다.');
            e.preventDefault();
        }
    });


}